import logo from './logo.svg';
import './App.css';
import Topbar from "./components/topbar";
import Filter from './components/filter/index.js';
import Card from './components/card';
import { Component, PureComponent } from 'react';
import axios from "axios";
import Footer from './components/footer';

class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: []
     }
  }
  componentDidMount() {
    axios.get("https://api.edyoda.com/v1/blog/")
      .then(res => {
        this.setState({ data: res.data })
        console.log(new Date(res.data[0].posted_on))
      });
      console.log("didMount")
  }
  renderData = (val) => {
    if(val === "all") {
      axios.get(`https://api.edyoda.com/v1/blog/`)
      .then(res => this.setState({data: res.data}))
    }  else {
    axios.get(`https://api.edyoda.com/v1/blog/${val}/`)
        .then(res => this.setState({data: res.data.posts}))
    }
  }
  render() {
    console.log("render")
    return (
      <div>
        < Topbar value={this.renderData}/>
        < Filter value={this.renderData}/>
        <section id="cards">
          {this.state.data.map((item) => (
            < Card details={item} date={item.posted_on}/>
          ))};
        </section>
        < Footer />
      </div>
    )

  }
}

export default App;
