import React from "react";
import $ from "jquery";
import "./style.css";
const Topbar = ({ value }) => {
    const data = (e) => {
        const str = e.currentTarget.innerHTML;
        const res = str.toLowerCase().split(" ")
        const val = res.join("-");
        value(val);
        const categories = document.getElementsByClassName("categories");
        for (let Category of categories) {
            Category.classList.remove("active");
        }
        document.getElementById(val).classList.add("active");
        document.getElementById("dropdown").classList.add("display-none")
    }

    const dropdown = () => {
        $(document).ready(function () {
            $("#dropdown").toggleClass("display-none")
        })

    }
    return (
        <nav>
            <div>
                <p id="logo">EDYODA</p>
                <span id="logo-stories-text">Stories</span>
            </div>
            <div id="dropdown-menu">
                <p onClick={dropdown} id="show-dropdown">
                    Explore Categories  <span id="dropdown-icon"><img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAGCAYAAAD37n+BAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAHhJREFUeNpiYGBguAPE/f///2fAh0FqgPguiBEDxC+BeDUQM2NRyAyVewlWCxW0B+L7QHwAiHmRFPNCxUBy9mAxJElVIL4MxBeBWB6KL0LFVOHq0KwXAuJDQHwPikFsIWQ1jFCFcMDIyMgGpFZAuRFA+V/I8gABBgD8EGd4shdx5wAAAABJRU5ErkJggg=="></img></span>
                </p>
                <div id="dropdown" className="display-none">
                    <p><a onClick={data} href="#">Artificial Intelligence</a></p>
                    <p><a onClick={data} href="#">Cloud Computing</a></p>
                    <p><a onClick={data} href="#">DevOps</a></p>
                    <p><a onClick={data} href="#">Programming Languages</a></p>
                    <p><a onClick={data} href="#">Mobile Application Development</a></p>
                    <p><a onClick={data} href="#">Technology and Tools</a></p>
                    <p><a onClick={data} href="#">Get a Job in a Tech Company</a></p>
                    <p><a onClick={data} href="#">Others</a></p>
                </div>
            </div>
            <p id="disclamer">
                EdYoda is free learning and knowledge
                sharing platform for techies</p>
            <div id="btn">Go To Main Website</div>
        </nav>
    )
}

export default Topbar;