import React from "react";
import "./style.css"
const Card = ({details , date}) => {
    const posted = new Date(date);
    const months = ["Jan","Feb","Mar","Apr","May","June","Jul","Aug","Sep","Oct","Nov","Dec"];
    const fullDate = ` ${posted.getDate()} ${months[posted.getMonth()]} ${posted.getFullYear()}`
    return(
        <div class="card">
        <div class="card-img">
          <img class="BlogCard__BlogImg__1kFRz" src={details.small_image} alt="Introduction To A Machine Learning Online Course"/>
        </div>
        <div class="text-section">
          <h3 class="title">
            {details.title}
          </h3>
          <h4 class="author-section"><span class="author">{details.authorname}</span> <b>|{fullDate}</b></h4>
          <div class="details-text">{details.description}</div>
        </div>  
      </div>
    )
}
export default Card;