import React from "react";
import "./style.css"
const Filter = ({value}) => {
    const data = (e) => {
        const str = e.currentTarget.innerHTML;
        const res = str.toLowerCase().split(" ")
        const val = res.join("-");
        value(val);
        const categories = document.getElementsByClassName("categories");
        for(let Category of categories) {
            Category.classList.remove("active");
        }
        document.getElementById(val).classList.add("active");
    }
    
    return (
        <article id="categories-container">
            <h1 id="heading">Latest Posts</h1>
            <div id="filter">
                <div id="filter-img">
                    <img alt="Filter Icon" src="data:image/png;base64,"></img>
                </div>
                <p id="filter-text">Filter by Category</p>
            </div>
            <div>
                <span onClick={data} id="all" class="categories active">All</span>
                <span onClick={data} id="artificial-intelligence" class="categories">Artificial Intelligence</span>
                <span onClick={data} id="cloud-computing" class="categories">Cloud Computing</span>
                <span onClick={data} id="devops" class="categories">DevOps</span>
                <span onClick={data} id="programming-languages" class="categories">Programming Languages</span>
                <span onClick={data} id="mobile-application-development" class="categories">Mobile Application Development</span>
                <span onClick={data} id="technology-and-tools" class="categories">Technology and Tools</span>
                <span onClick={data} id="get-a-job-in-a-tech-company" class="categories">Get a Job in a Tech Company</span>
                <span onClick={data} id="others" class="categories">Others</span>
            </div>
        </article>
    )
}

export default Filter;